
import moment from 'moment'
export const dateFormat = {
  methods: {
    dateFormat (val) {
      return moment(val).format('YYYY-MM-DD')
    }
  }
}
export const getSexByName = {
  methods: {
    getSexByName (type) {
      if (type === 1) {
        return '男'
      } else {
        return '女'
      }
    }
  }
}

export const getRelation = {
  methods: {
    getRelation (type) {
      switch (parseInt(type)) {
        case 0:
          return '父亲'
          break
        case 1:
          return '母亲'
          break
        case 2:
          return '儿子'
          break
        case 3:
          return '女儿'
          break
        case 4:
          return '妻子'
          break
        case 5:
          return '丈夫'
          break
        case 6:
          return '本人'
          break
        default:
          return '其他'
      }
    }
  }
}

export const orderStatus = {
  methods: {
    orderStatus (type) {
      switch (type) {
        case 0:
          return '待支付'
          break
        case 1:
          return '已支付'
          break
        case 2:
          return '已承保'
          break
        case 3:
          return '已出单'
          break
        case 4:
          return '保障中'
          break
        case 5:
          return '已失效'
          break
        case 6:
          return '订单失败'
          break
        case 7:
          return '已取消'
          break
        case 8:
          return '支付失败'
          break
      }
    }
  }
}
