<template>
  <div class="product-detail layout">
    <detail-top :topList="detailData" :isFlag="isFlag"></detail-top>
    <div class="detail-content clearfix">
      <div class="leftDetail">
        <detail-introduce :introduce="detailData" :parameters="parameters"></detail-introduce>
      </div>
    <div ref="rightDetail" id="rightDetail" class="rightDetail" v-sticky="{top:0}">
        <detail-calculation v-if="token"></detail-calculation>
        <detail-applicant :price="price"  v-else></detail-applicant>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import detailTop from '@/components/product/detail/TheDetailTop'
import detailIntroduce from '@/components/product/detail/TheDetailIntroduce'
import detailCalculation from '@/components/product/detail/TheDetailCalculation'
import detailApplicant from '@/components/product/detail/TheDetailApplicant'
import Sticky from '@/assets/plugins/sticky.js'
import { getLocalStorage } from '@/assets/js/localStorage'

export default {
  name: 'TheDetailMain',
  data () {
    return {
      detailData: {},
      searchBarFixed: false,
      memberId: '',
      productId: '',
      parameters: [],
      isFlag: '',
      price: 0
    }
  },
  mounted () {
    if (getLocalStorage('userInfo')) {
      this.memberId = getLocalStorage('userInfo').id
    }
    this.getDetail()
  },
  methods: {
    getDetail () {
      let postData = {
        id: this.$route.query.productId,
        memberId: this.memberId
      }
      this.$api.product.productDetail(postData).then(res => {
        this.detailData = res.data.product
        if (res.data.flag === '') {
          this.isFlag = '1'
        } else {
          this.isFlag = res.data.flag
        }
        this.price = res.data.product.price
        if (res.data.product.parameter_values !== null) {
           this.parameters = res.data.product.parameter_values[0].entries
        }
      })
    }
  },
  computed: {
    ...mapGetters([
      'token'
    ])
  },
  directives: {
    Sticky
  },
  components: { detailTop, detailIntroduce, detailCalculation, detailApplicant }
}
</script>

<style scoped lang="scss">
  .leftDetail {
    margin-bottom: 92px;
    float: left;
  }

  .rightDetail {
    margin-bottom: 20px;
    float: right;
    &.isFixed {
      position: fixed;
      top: 10px;
    }
  }
</style>
