<template>
  <div class="product-calculation">
    <p class="title">保费测算</p>
    <div class="content">
      <div class="layer clearfix">
        <div class="fl">投保人</div>
        <div class="fr">{{userInfo.name}}</div>
      </div>
      <div class="layer clearfix">
        <div class="fl">为谁投保</div>
        <div class="fr icon_insure" @click="insuredShow=true">{{insuredName}}</div>
      </div>
      <div class="layer clearfix">
        <div class="fl">保险期间</div>
        <div class="fr">1天</div>
      </div>
      <div class="layer clearfix">
        <div class="fl">生效日期</div>
        <!--<i class="icon_select"  :style="createDateShow ? downImg : upImg "></i>-->
        <div class="fr date">
          <el-date-picker
            v-model="startDate"
            type="date"
            :editable="false"
            @change="changeStartDate"
            :picker-options="pickerOptions"
            placeholder="选择日期">
          </el-date-picker>

        </div>
      </div>
      <div class="layer clearfix">
        <div class="fl">终止日期</div>
        <div class="fr">{{endDate}}</div>
      </div>
      <div class="layer clearfix">
        <div class="fl">计划选择</div>
        <div class="fr">
          <ul class="clearfix plan-name">
            <li v-for="(item,index) in plans" :key="index" @click="changePlan(index)"
                :class="currentPlanIndex==index?'active':''">{{item.plan_name}}
            </li>
          </ul>
        </div>
      </div>
      <div class="plan-con">
        <p class="ins">保障方案</p>
        <ul class="plans-con-wrapper">
          <li v-for="(plancon,index) in planCons" v-show=" index == currentPlanIndex" :key="index">
            <p class="clearfix item" v-for="(item,index) in plancon" :key="index">
              <span class="fl">{{item.name}}</span>
              <span class="fr">{{item.value}}</span>
            </p>
          </li>
        </ul>
      </div>
      <div class="price-insure clearfix">
        <p class="price fl">
          {{totalPrice}}<span>元</span>
        </p>
        <p class="insrue-btn fr" @click="sumlime">
          立即投保
        </p>
      </div>
    </div>
    <div class="insured-pop" v-if="insuredShow" id="insured">
      <i class="close" @click="insuredShow=false"></i>
      <ul>
        <li class="clearfix" @click="seletcInsured(item)" v-for="(item,index) in insuredList" :key="index">
          <span class="name">{{item.name}}</span>
          <span class="relation">{{getRelation(item.type)}}</span>
          <span class="idnum">{{item.id_num}}</span>
        </li>
      </ul>
      <div class="add-btn-wrapper clearfix">
        <span @click="addInsuredShow=true">新增被保险人</span>
      </div>
    </div>
    <insuredAdd
      :isShow="addInsuredShow"
      @cancel="addInsuredShow=false"
    ></insuredAdd>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import moment from 'moment'
import { getRelation } from '@/assets/js/mixins/base.js'
import insuredAdd from '@/components/TheInsured'

export default {
  name: 'TheDetailCalculation',
  mixins: [getRelation],
  components: {
    insuredAdd
  },
  data () {
    return {
      pickerOptions: {
        disabledDate (time) {
          return time.getTime() < Date.now()
        }
      },
      amemdPlanId: '',
      amendBbxrName: '',
      addInsuredShow: false,
      currentPlanIndex: 0,
      createDateShow: false,
      insuredShow: false,
      upImg: {
        'background-image':
          'url(' + require('@/assets/images/icon_down.png') + ')'
      },
      downImg: {
        'background-image':
          'url(' + require('@/assets/images/icon_up.png') + ')'
      },
      plans: [],
      planCons: [],
      startDate: moment(new Date()).add(1, 'days').format('YYYY-MM-DD'),
      endDate: moment(new Date()).add(1, 'days').format('YYYY-MM-DD'),
      calculationFactors: [],
      plan_choice: '',
      totalPrice: '',
      planDefault: '',
      insuredList: {},
      bbxrName: '',
      insured: {
        relationId: '',
        skuId: '',
        planId: ''
      }
    }
  },
  mounted () {
    if (!this.$route.query.orderSn) {
      this.getPlans()
      this.getCalculationFactors()
    }

    this.getInsurdList()
    if (this.$route.query.orderSn) {
      this.getCalculationFactors().then(res => {
        if (res) {
          this.amendOrder().then(res => {
            if (res) {
              this.getPlans()
            }
          })
        }
      })
    }
  },
  methods: {
    sumlime () {
      let postData = {
        skuId: this.insured.skuId,
        planId: this.insured.planId,
        productId: this.$route.query.productId,
        relationId: this.insured.relationId,
        effDate: this.startDate,
        matuDate: this.endDate
      }
      this.$api.product.orderSubnit(postData).then(res => {
        if (res.code === 1) {
          this.$router.push({
            path: '/37duc/product/orderConfirm',
            query: {
              sn: res.data
            }
          })
        } else {
          this.$toast.center(res.message)
        }
      })
    },
    amendOrder () {
      return new Promise((resolve, reject) => {
        let postData = {
          orderSn: this.$route.query.orderSn
        }
        this.$api.product.amendOrder(postData).then(res => {
          if (res.code === 1) {
            this.startDate = moment(res.data.policy_effective_date).format('YYYY-MM-DD')
            this.endDate = moment(res.data.policy_expiration_date).format('YYYY-MM-DD')
            this.amemdPlanId = res.data.plan_info.id
            this.amendBbxrName = res.data.relation_info.name

            this.totalPrice = res.data.price
            this.plan_choice = res.data.plan_info.id
            resolve(res.data)
          } else {
            this.$toast.center(res.message)
          }
        })
      })
    },
    getCalculationFactors () {
      return new Promise((resolve, reject) => {
        let postData = {
          productId: this.$route.query.productId
        }
        this.$api.product.getCalculationFactors(postData).then(res => {
          if (res.code === 1) {
            resolve(res)
            this.calculationFactors = res.data
            for (let i = 0; i < res.data.length; i++) {
              if (res.data[i].is_default) {
                this.currentPlanIndex = i
                this.plan_choice = res.data[i].plan_id
              }
            }
            this.matchPrice(this.calculationFactors, this.plan_choice)
          }
        })
      })
    },
    matchPrice (data, nameId) {
      data.forEach((item) => {
      //console.log(item.plan_id, item.plan_id === nameId, item.calculation_factors, item.calculation_factors.length, item.calculation_factors[0].name === '天数')
        if (item.plan_id && item.plan_id === nameId &&
            item.calculation_factors && item.calculation_factors.length && item.calculation_factors[0].name === '天数') {
          this.totalPrice = item.sku_price
          this.insured.skuId = item.id
          this.insured.planId = item.plan_id
          return false
        }
      })
      return this.totalPrice
    },
    getPlans () {
      let postData = {
        productId: this.$route.query.productId
      }
      this.$api.product.getPlans(postData).then(res => {
        if (res.code === 1) {
          // this.plan_choice = res.data[0].id
          for (let i = 0; i < res.data.length; i++) {
            if (res.data[i].id === this.amemdPlanId) {
              this.currentPlanIndex = i
            }
            this.plans = this.plans.concat(res.data[i])
            this.planCons[i] = this.plans[i].plan_details
          }
        }
      })
    },
    changePlan (index) {
      this.currentPlanIndex = index
      this.plan_choice = this.plans[index].id
    },
    getInsurdList () {
      let postData = {
        page: 1,
        limit: 1000
      }
      this.$api.personCenter.getInsured(postData).then(res => {
        if (res.code === 1) {
          this.insuredList = res.data.data
          this.insuredList.forEach((item, index) => {
            if (item.type === 6) {
              this.insured.relationId = item.id
            }
          })
        }
      })
    },
    changeStartDate (val) {
      this.startDate = moment(val).format('YYYY-MM-DD')
    },
    seletcInsured (item) {
      this.bbxrName = item.name
      this.insuredShow = false
      this.insured.relationId = item.id
    }
  },
  watch: {
    startDate (val) {
      this.endDate = moment(val).format('YYYY-MM-DD')
    },
    plan_choice (val) {
      this.matchPrice(this.calculationFactors, val)
    }
  },
  computed: {
    insuredName () {
      if (this.bbxrName) {
        return this.bbxrName
      } else if (this.$route.query.orderSn) {
        return this.amendBbxrName
      } else {
        return this.userInfo.name
      }
    },
    ...mapGetters([
      'userInfo'
    ])
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";

  .product-calculation {
    position: relative;
    width: 320px;
    background: #fff;
    padding: 20px 20px 20px 18px;
    box-shadow: 0 2px 4px 0 rgba(219, 219, 219, 0.35);
    .title {
      font-size: 20px;
      color: $color-text;
      font-weight: bold;
      margin-bottom: 16px;
    }
    .content {
      .layer {
        position: relative;
        border-bottom: 1px solid #F1F4F4;
        padding: 13px 0 12px;
        font-size: 14px;
        .plan-name {
          max-width: 228px;
          margin-top: -4px;
          li {
            float: left;
            font-size: 12px;
            color: #979797;
            border: 1px solid #E8E8E8;
            border-radius: 2px;
            height: 24px;
            line-height: 24px;
            padding: 0 6px;
            cursor: pointer;
            &:not(:last-child) {
              margin-right: 8px;
            }
            &.active {
              background: #3875E2;
              color: #FFFFFF;
            }
          }
        }
        .date {
          background-size: 16px 16px;
          /*background-repeat: no-repeat;*/
          background-position: center center;
        }
        .icon_insure {
          background-size: 16px 16px;
          background: url("../../../assets/images/icon_right.png") no-repeat right top;
          padding-right: 24px;
          cursor: pointer;
        }
        .icon_select {
          display: inline-block;
          width: 16px;
          height: 16px;
          cursor: pointer;
        }
      }
      .plan-con {
        .ins {
          font-size: 14px;
          color: $color-text;
          margin: 12px 0 18px;
        }
        .plans-con-wrapper {
          .item {
            font-size: 12px;
            margin-bottom: 12px;
          }
        }
      }
      .price-insure {
        margin-top: 34px;
        .price {
          font-size: 30px;
          color: $color-active-text;
          line-height: 44px;
          span {
            font-size: 12px;
            color: #D5D8D8;
          }
        }
        .insrue-btn {
          @include insured-btn;
        }
      }
    }
    .insured-pop {
      position: absolute;
      top: 132px;
      left: 30px;
      width: 298px;
      box-shadow: 0 8px 16px 0 #DBDBDB;
      border-radius: 2px;
      background: #Fff;
      padding: 40px 0 20px;
      .close {
        cursor: pointer;
        background: url("../../../assets/images/pop_close.png") no-repeat;
        background-size: 22px 22px;
        width: 20px;
        height: 20px;
        display: block;
        position: absolute;
        top: 10px;
        right: 10px;
      }
      ul {
        li {
          font-size: 14px;
          color: #333333;
          height: 38px;
          line-height: 38px;
          border-bottom: 1px solid #F1F4F4;;
          &:hover {
            background: #F1F4F4;
          }
          span {
            float: left;
            margin-right: 20px;
            &.name {
              width: 42px;
              padding-left: 20px;
              @include text-ellipsis;
            }
            &.idnum {
              float: right;
            }
          }
        }
      }
      .add-btn-wrapper {
        padding-right: 14px;
        margin-top: 20px;
        span {
          display: block;
          float: right;
          cursor: pointer;
          width: 120px;
          height: 36px;
          line-height: 36px;
          border: 1px solid #1CD7D1;
          border-radius: 18px;
          font-size: 14px;
          color: $color-active-text;
          text-align: center;
        }
      }
    }
  }
</style>
