<template>
  <div class="detail-applicant">
    <div class="calculate" v-if="!applicantShow">
      <p class="title">保费测算</p>
      <p class="price">
        {{ price}}<span>元起</span>
      </p>
      <div class="insure-btn" @click="applicantShow=true">立即投保</div>
    </div>
    <div class="applicant" v-if="applicantShow">
      <p class="title">填写投保人信息</p>
      <p class="sub-title">请先填写投保人信息后，再进行保费测算</p>
      <form action="" class="comForm" @submit.prevent="submit">
        <div class="input-wrapper">
          <input type="text" v-model.trim="sendForm.idNum" :class="errorIdNum?'active':''"
                 @focus="focus($event,'idNum')" @blur="blur($event,'idNum')" placeholder="身份证号">
          <i class="del" v-show="sendForm.idNum" @mousedown.stop="del('idNum')" :style="delImg"></i>
          <span class="error">{{errorIdNum}}</span>
        </div>
        <div class="input-wrapper">
          <input type="text" v-model.trim="sendForm.queryName" @focus="focus($event)" @blur="blur($event)"
                 :readonly=isRegister placeholder="姓名">
          <i class="del" v-show="sendForm.queryName&&!isRegister" @mousedown.stop="del('queryName')"
             :style="delImg"></i>
        </div>
        <div class="input-wrapper">
          <input type="text" v-model.trim="sendForm.queryMobile" :class="errorMobile?'active':''"
                 :readonly=isRegister @focus="focus($event,'mobile')" @blur="blur($event,'mobile')" placeholder="手机号">
          <i class="del" v-show="sendForm.queryMobile&&!isRegister" @mousedown.stop="del('queryMobile')"
             :style="delImg"></i>
          <span class="error">{{errorMobile}}</span>
        </div>
        <div class="input-wrapper msgcode-wrapper clearfix">
          <input type="text" v-model.trim="sendForm.msgCode" :class="errorMsgCode?'active':''"
                 @focus="focus($event,'msgCode')"
                 @blur="blur($event,'msgCode')" placeholder="短信验证码">
          <i class="del" v-show="sendForm.msgCode" @mousedown.stop="del('msgCode')" :style="delImg"></i>
          <span class="aboutcode" v-if="!isMessageCode" @click="getMessageCode()">发送验证码</span>
          <span class="aboutcode sendcode" v-else>{{second}}s重发</span>
          <span class="error ">{{errorMsgCode}}</span>
        </div>
        <p class="tips">请确保信息填写正确，后续您可以通过身份证号码及手机验证码查询订单等信息。</p>
        <div class="submit-wrapper">
          <!--<p class="mobileSendTips" v-if="mobileSendShow">验证码已发送至尾号为{{this.currentMobile}}的手机号</p>-->
          <div class="error-tips" v-show="errorTips">{{errorTips}}</div>
          <input type="submit" value="提交" class="submit" :class="allValid? 'active':''" :disabled="!allValid">
        </div>
        <img-code v-if="imgCodeShow"
                  :absTop="-78"
                  :absLeft="-20"
                  @hanleClose="imgCodeShow=false">
          <div class="imgcode-content">
            <div class="input-wrapper">
              <input type="text" class="input-imgcode" placeholder="请输入图形验证码"
                     @focus="focus($event,'imgCode')" @blur="blur($event,'imgCode')"
                     v-model.trim="sendForm.queryImgCode">
              <i class="del" v-show="sendForm.queryImgCode" @mousedown.stop="del('queryImgCode')" :style="delImg"></i>
              <img ref="imgCode" :src="imgCodeSrc" alt="图形验证码" class="imgcode" @click="refreshImgcode">
              <span class="error">{{errorImgCode}}</span>
            </div>
            <input type="button" class="imgcode-btn" value="确定"
                   @click="validImgcode"
                   :class="sendForm.queryImgCode.length>=4?'active':''"
                   :disabled="sendForm.queryImgCode.length<4"
            >
          </div>
        </img-code>
      </form>

    </div>
  </div>
</template>

<script>
import { getComFormMixin } from '@/assets/js/mixins/comForm'
import { ValidateUtil } from '@/assets/js/global'
import { mobileErrorTips } from '@/assets/js/errorTips'
import imgCode from '@/components/TheImgCode.vue'
import { setLocalStorage, getLocalStorage } from '@/assets/js/localStorage'
import { mapMutations } from 'vuex'

export default {
  name: 'TheDetailApplicant',
  mixins: [getComFormMixin],
  props: {
    price: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      applicantShow: false,
      isRegister: false, // 身份证号是否已注册
      imgCodeSrc: '',
      second: 60,
      sendForm: {
        idNumL: '',
        queryMobile: '',
        queryImgCode: '',
        queryName: '',
        queryMobileFull: ''

      }
    }
  },
  mounted () {
    this._isCountDown()
  },
  methods: {
    submit () {
      let _mobile
      if (this.isRegister) {
        _mobile = this.sendForm.queryMobileFull
      } else {
        _mobile = this.sendForm.queryMobile
      }
      let postData = {
        idNum: this.sendForm.idNum,
        mobile: _mobile,
        name: this.sendForm.queryName,
        captchaId: this.sendForm.msgCode,
        hiddenCaptcha: this.uuid
      }
      this.$api.product.tbSubmit(postData).then(res => {
        if (res.code === 1) {
          this.SET_TOKEN(res.data.csrfToken)
          this.SET_USER_INFO(res.data.member)
        } else {
          if (res.message.includes('短信验证码错误')) {
            this.errorMsgCode = res.message
          } else {
            this.errorTips = res.message
          }
        }
      })
    },
    sendCaptchaSms () {
      let _mobile
      if (this.isRegister) {
        _mobile = this.sendForm.queryMobileFull
      } else {
        _mobile = this.sendForm.queryMobile
      }
      let postData = {
        idNum: this.sendForm.idNum,
        mobile: _mobile,
        captchaId: this.uuid
      }
      this.$api.product.tbSendCaptchaSms(postData).then(res => {
        if (res.code === 1) {
          this.readsecond()
        } else {
          this.errorMobile = res.message
        }
      })
    },
    getMessageCode () {
      if (this.errorMobile) return
      if (!this.sendForm.queryMobile) {
        this.errorMobile = mobileErrorTips.notNull
        return
      }
      this.imgCodeShow = true
      this.refreshImgcode()
      this.sendForm.queryImgCode = ''
      // this.sendCaptchaSms()
    },
    validImgcode () {
      let postData = {
        code: this.sendForm.queryImgCode
      }
      this.$api.product.tbValidImgcode(postData).then(res => {
        if (res.code === 1) {
          this.imgCodeShow = false
          this.sendCaptchaSms()
        } else {
          this.refreshImgcode()
          this.errorImgCode = res.message
        }
      })
    },

    refreshImgcode () {
      let _random = Math.random()
      this.imgCodeSrc = `${process.env.VUE_APP_BASE_URL}/shop/product/insureCaptcha?v=${_random}`
    },
    isIdNumRegister () {
      let postData = {
        idNum: this.sendForm.idNum
      }
      this.$api.product.tbIdNumValid(postData).then((res) => {
        if (res.code === 1) {
          this.sendForm.queryMobileFull = res.data.mobile
          this.sendForm.queryMobile = res.data.mobile.substr(0, 3) + '****' + res.data.mobile.substr(7)
          this.sendForm.queryName = res.data.name.substr(0, 1) + '*' + res.data.name.substring(2, res.data.length)
          this.isRegister = true
        } else {
          this.errorIdNum = res.message
          this.isRegister = false
        }
      })
    },
    readsecond () {
      this.isMessageCode = true
      var timer = setInterval(() => {
        if (this.second <= 1) {
          this.isMessageCode = false
          this.second = 60
          clearInterval(timer)
          return
        }
        this.second--
        setLocalStorage('tbSecond', this.second)
      }, 1000)
    },
    blur (event, name) {
      this._onBlur(event)
      switch (name) {
        case 'idNum':
          this._validIdNum()
          break
        case 'mobile':
          this._validMobile()
          break
      }
    },
    isMobileBind () {
      let postData = {
        mobile: this.sendForm.queryMobile
      }
      this.$api.user.isMobileBind(postData).then((res) => {
        if (res.code === 0) {
          // 1：未绑定 0：绑定
          this.errorMobile = mobileErrorTips.binded
        }
      })
    },
    _isCountDown () {
      let sec = getLocalStorage('tbSecond')
      if (sec > 1) {
        this.second = sec
        this.isMessageCode = true
        this.readsecond()
      }
    },
    _validMobile () {
      if (this.isRegister && this.sendForm.queryMobileFull) {
        if (!ValidateUtil.mobileRegexs(this.sendForm.queryMobileFull).pass) {
          this.errorMobile = ValidateUtil.mobileRegexs(this.sendForm.queryMobileFull).tip
          return
        }
      }
      if (!this.isRegister && this.sendForm.queryMobile) {
        if (!ValidateUtil.mobileRegexs(this.sendForm.queryMobile).pass) {
          this.errorMobile = ValidateUtil.mobileRegexs(this.sendForm.queryMobile).tip
          return
        }
      }
      this.isMobileBind()
    },
    _validIdNum () {
      if (this.sendForm.idNum) {
        if (!ValidateUtil.idCardRegexs(this.sendForm.idNum).pass) {
          this.errorIdNum = ValidateUtil.idCardRegexs(this.sendForm.idNum).tip
          return
        }
        if (!ValidateUtil.years16Regexs(this.sendForm.idNum).pass) {
          this.errorIdNum = '仅支持16周岁及以上用户投保'
          return
        }
        this.isIdNumRegister()
      }
    },
    ...mapMutations([
      'SET_TOKEN',
      'SET_USER_INFO'
    ])
  },
  watch: {
    'sendForm.idNum' (curVal, oldVal) {
      if (curVal !== oldVal) {
        this.sendForm.queryName = ''
        this.sendForm.queryMobile = ''
        this.isRegister = false
      }
    }
  },
  computed: {
    allValid () {
      return this.sendForm.idNum && this.sendForm.queryName &&
          this.sendForm.queryMobile && this.sendForm.msgCode &&
          !this.errorIdNum && !this.errorMobile
    }
  },
  components: {
    imgCode
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";
  @import "@/assets/styles/comForm.scss";

  .detail-applicant {
    width: 318px;
    padding: 20px;
    background: #fff;
    box-shadow: 0 2px 6px 0 rgba(219, 219, 219, 0.60);
    .title {
      font-size: 20px;
      color: $color-text;
      font-weight: bold;
    }
    .calculate {
      padding-bottom: 10px;
      .price {
        margin: 79px 0 45px;
        text-align: center;
        font-size: 30px;
        color: $color-active-text;
        span {
          font-size: 12px;
          color: #D5D8D8;
        }
      }
      .insure-btn {
        margin: 0 auto;
        cursor: pointer;
        @include insured-btn
      }
    }
    .applicant {
      .sub-title {
        font-size: 12px;
        color: #B6BBBB;
        margin: 20px 0 27px;
      }
      .input-wrapper {
        input {
          padding: 3px 0 14px;
          font-size: 14px;
        }
        i.del {
          bottom: 36px;
        }
        .error {
          bottom: 6px;
        }
      }
      .msgcode-wrapper {
        input {
          width: 208px;
        }
        i.del {
          right: 111px;
        }
        .aboutcode {
          width: 94px;
          height: 36px;
          line-height: 36px;
          margin-top: -2px;
        }
      }
      .tips {
        font-size: 12px;
        color: #333333;
        line-height: 18px;
      }
      .submit-wrapper {
        text-align: center;
        .submit {
          margin: 0 auto;
          margin-top: 16px;
          @include insured-btn
        }
      }
    }
  }
</style>
