<template>
  <div class="detail-top clearfix">
    <div class=" top-left">
      <p class="title">{{topList.name}}</p>
      <p class="condition clearfix">
        <span class="condition-item" v-for="(item,index) in topList.insure_condition" :key="index">{{item.name}}：{{item.value}}</span>
      </p>
      <p class="product-ts">
        <span class="ts-item"  v-if="topList.feature1"><i></i>{{topList.feature1}}</span>
        <span class="ts-item"  v-if="topList.feature2"><i></i>{{topList.feature2}}</span>
        <span class="ts-item"  v-if="topList.feature3"><i></i>{{topList.feature3}}</span>
      </p>
    </div>
    <div class=" top-right ">
      <div class="company-logo">
        <img :src="baseSrc+topList.brandLogo" alt="公司logo">
      </div>
      <div class="protocal-wrapper">
        <span @click="goProtocalList"><img src="../../../assets/images/icon_clause.png" alt="">保险条款</span>
        <span  @click="notifyShow=true"><img src="../../../assets/images/icon_notify.png" alt="" >客户告知书</span>
        <span @click="goSample" v-if="topList.policy_sample_url"><img src="../../../assets/images/icon_sample.png" alt="">保单样本书</span>
      </div>
      <div class="collect">
        <i :style="collectImg" @click="collectOrNot"></i>收藏
      </div>
    </div>
    <pop-box   :title="'客户告知书'" v-if="notifyShow"
               :width="874"
               :height="650"
               :content="notifyCon"
               @dealClose="handleNoticyClose">></pop-box>
  </div>
</template>

<script>
import popBox from '@/components/BasePopBox'
import { mapGetters } from 'vuex'
export default {
  name: 'TheDetailTop',
  props: {
    topList: {
      type: Object,
      default: () => {
        return {}
      }
    },
    isFlag: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      notifyShow: false,
      sampleCon: '',
      baseSrc: '',
      isCollect: false,
      notifyCon: ''
    }
  },
  mounted () {
    this.baseSrc = `${process.env.VUE_APP_RESOURCE_URL}`
    this.getNotify()

    // 1未收藏 0 收藏
  },
  methods: {
    handleNoticyClose () {
      this.notifyShow = false
    },
    goProtocalList () {
      this.$router.push({
        path: '/37duc/product/protocal',
        query: {
          productId: this.$route.query.productId,
          productName: this.topList.name
        }
      })
    },
    goSample () {
      window.open(`${process.env.VUE_APP_RESOURCE_URL}/${this.topList.policy_sample_url}`)
    },
    getNotify () {
      let postData = {
        id: this.$route.query.productId
      }
      this.$api.product.getNoticeBook(postData).then(res => {
        this.notifyCon = res.data
      })
    },
    collectOrNot () {
      if (this.token) {
        let postData = {
          productId: this.$route.query.productId
        }
        this.$api.personCenter.collectOrNot(postData).then(res => {
          if (res.code === 1) {
            this.isCollect = !this.isCollect
            if (this.isCollect) {
              this.$toast.center('已收藏')
            } else {
              this.$toast.center('已取消收藏')
            }
          }
        })
      } else {
        this.$router.push({
          name: 'SmsLogin',
          params: {
            redirctUrl: this.$route.path,
            productId: this.$route.query.productId
          }
        })
      }
    },
    toNewPage (url) {
      window.open(url)
    }
  },
  computed: {
    ...mapGetters([ 'token']),
    collectImg () {
      return this.isCollect && this.token ? {
        'background-image':
        'url(' + require('@/assets/images/icon_collected.png') + ')'
      } : {
        'background-image':
        'url(' + require('@/assets/images/icon_collect.png') + ')'
      }
    }
  },

  watch: {
    'isFlag': {
      handler (val) {
        this.isCollect = val === '0'
      }
    }
  },
  components: {
    popBox
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";

  .detail-top {
    background: #fff;
    margin: 12px 0;
    padding: 20px 20px 16px;
    box-shadow: 0 2px 4px 0 rgba(219, 219, 219, 0.35);
    .top-left {
      float: left;
      max-width: 642px;
      .title {
        font-size: 20px;
        color: $color-text;
        font-weight: bold;
      }
      .condition {
        margin: 16px 0 21px;
        .condition-item {
          font-size: 14px;
          color: #B6BBBB;
          &:not(:last-child):after {
            content: '|';
          }
        }
      }
      .product-ts {
        .ts-item {
          font-size: 14px;
          color: $color-text;
          margin-right: 40px;
          i {
            display: inline-block;
            vertical-align: top;
            margin-top: 1px;
            background: url("../../../assets/images/icon_ts.png") no-repeat;
            background-size: 16px 16px;
            width: 16px;
            height: 16px;
            margin-right: 8px;
          }
        }

      }
    }
    .top-right {
      float: right;
      max-width: 374px;
      .company-logo {
        float: right;
        width: 80px;
        height: 40px;
        overflow: hidden;
        img {
          width: 80px;
          height: 40px;
        }
      }
      .protocal-wrapper {
        padding-right:100px;
        margin-top: 72px;
        span {
          cursor: pointer;
          font-size: 12px;
          color: #3875E2;
          &:not(:last-child) {
            margin-right: 21px;
          }
          img {
            width: 16px;
            height: 16px;
            vertical-align: top;
            margin-right: 5px;
            padding-top: 1px;
          }
        }
      }
      .collect {
        position: absolute;
        top: 92px;
        right: 20px;
        cursor: pointer;
        i {
          display: inline-block;
          vertical-align: middle;
          width: 24px;
          height: 24px;
          background-repeat: no-repeat;
          margin: -5px 6px 0;
        }
      }
    }
  }

</style>
