<template>
  <div class="AddTheInsured" v-show='isShow'>
    <div class="cover"></div>
    <div class="content">
      <div class="ttl">{{title}}</div>
      <div class="input-box">
        <div class="input-item">
          <span class="lb">姓名:</span>
          <div class="input">
            <run-input v-model='form.name.value' :error='form.name.error' :placeholder="'请输入姓名'"
                       @focus='focus(form.name)'></run-input>
          </div>
        </div>
        <div class="input-item">
          <span class="lb">与投保人的关系:</span>
          <div class="input">
            <run-select v-model='form.insurer.value' :limit='7' :options='form.insurer.options' :error='form.insurer.error' :placeholder="'请选择'" @handlechange='handlechange'></run-select>
          </div>
        </div>
        <div class="input-item">
          <span class="lb">身份证号:</span>
          <div class="input">
            <run-input v-model='form.identity.value' :error='form.identity.error' :placeholder="'请输入身份证号'"
                       @focus='focus(form.identity)' @blur='blurIdentity'></run-input>
          </div>
        </div>
        <div class="cut-line">以下为选填项:</div>
        <div class="input-item">

          <span class="lb">手机号:</span>
          <div class="input">
            <run-input v-model='form.telphone.value' :error='form.telphone.error' :placeholder="'请输入手机号'"
                       @focus='focus(form.telphone)' @blur='blurTelphone'></run-input>
          </div>
        </div>
        <div class="input-item">
          <span class="lb">地址:</span>
          <div class="input-group">
            <div class="input-group-item address clearfix">
              <div class="adrs fl">
                <run-select v-model='form.address.province.value' :error='form.address.province.error'
                            :options='form.address.province.options' :placeholder="'省'"></run-select>
              </div>
              <div class="adrs fl">
                <run-select v-model='form.address.city.value' :error='form.address.city.error'
                            :options='form.address.city.options' :placeholder="'市'"></run-select>
              </div>
              <div class="adrs fr">
                <run-select v-model='form.address.county.value' :error='form.address.county.error'
                            :options='form.address.county.options' :placeholder="'区'"></run-select>
              </div>
            </div>
            <div class="input-group-item">
              <div class="input">
                <run-input v-model='form.address.road.value' :error='form.address.road.error' :placeholder="'街道地址'"
                           @focus='focus(form.address.road)'></run-input>
              </div>
            </div>
          </div>
        </div>
        <div class="input-item">
          <span class="lb">电子邮箱:</span>
          <div class="input">
            <run-input v-model='form.email.value' :error='form.email.error' :placeholder="'请输入'"
                       @focus='focus(form.email)' @blur='blurEmail'></run-input>
          </div>
        </div>
        <div class="btns">
          <div class="btn cancel" @click="cancel">取消</div>
          <div class="btn save" @click="submit" :class="{disabled: !isPass}">保存</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ValidateUtil } from '@/assets/js/global'
import { getRelation } from '@/assets/js/mixins/base.js'

export default {
  mixins: [getRelation],
  props: {
    isShow: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: '新增常用被保险人'
    }
  },
  data () {
    return {
      form: {
        name: {
          value: '',
          error: {
            err: false,
            msg: ''
          }
        },
        insurer: {
          value: '',
          options: [],
          error: {
            err: false,
            msg: ''
          }
        },
        identity: {
          value: '',
          error: {
            err: false,
            msg: ''
          }
        },
        telphone: {
          value: '',
          error: {
            err: false,
            msg: ''
          }
        },
        address: {
          province: {
            value: '',
            options: [
              {
                label: '福建省',
								value: '福建省',
                select: false,
                disabled: true
              },
              '浙江省'
            ],
            error: {
              err: false,
              msg: ''
            }
          },
          city: {
            value: '',
            options: ['厦门市'],
            error: {
              err: false,
              msg: ''
            }
          },
          county: {
            value: '',
            options: ['思明区'],
            error: {
              err: false,
              msg: ''
            }
          },
          road: {
            value: '',
            error: {
              err: false,
              msg: ''
            }
          }
        },
        email: {
          value: '',
          error: {
            err: false,
            msg: 'error'
          }
        }
      }
    }
  },
  mounted () {
		this.getInsuredType()
  },
  methods: {
    getInsuredType () {
      this.$api.personCenter.insuredType().then(res => {
        if (res.code === 1) {
          res.data.forEach((item, key) => {
            let temp = {};
            temp.label = this.getRelation(item[0]);
						temp.value = item[0];
            if (item[1] === '存在') {
              temp.disabled = true
            }
            this.form.insurer.options.push(temp)
          })
        }
      })
    },
    focus (data) {
      data.error.err = false
      data.error.msg = ''
    },
    blurIdentity () {
      if (this.form.identity.value && !ValidateUtil.idCardRegexs(this.form.identity.value).pass) {
        this.form.identity.error.err = true
        this.form.identity.error.msg = ValidateUtil.idCardRegexs(this.form.identity.value).tip
      }
    },
    blurTelphone () {
      if (this.form.telphone.value && !ValidateUtil.mobileRegexs(this.form.telphone.value).pass) {
        this.form.telphone.error.err = true
        this.form.telphone.error.msg = ValidateUtil.mobileRegexs(this.form.telphone.value).tip
      }
    },
    blurEmail () {
      var reg = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/i
      if (this.form.email.value && !reg.test(this.form.email.value)) {
        this.form.email.error.err = true
        this.form.email.error.msg = '电子邮箱格式错误'
      }
    },

    submit () {
      if (!this.isPass) return
      let form=this.form;
      let postData = {
				'id_num': form.identity.value,
				'type': parseInt(form.insurer.value),
				'name': form.name.value
			}
      this.$api.personCenter.addInsured(postData).then(res => {
        console.log(9999)
      })
    },
    cancel () {
      this.$emit('cancel', false)
    },
    handlechange (nval, oval) {
      console.log('nval', nval)
      console.log('oval', oval)
    },
    handleSelect (nval) {
      this.form.insurer.value = nval
    }
  },
  computed: {
    checkIdentity () {
      return ValidateUtil.idCardRegexs(this.form.identity.value).pass
    },
    checkTelphone () {
      return ValidateUtil.mobileRegexs(this.form.telphone.value).pass
    },
    checkEmail () {
      return /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/.test(this.form.email.value)
    },
    isPass () {
      return this.form.identity.value &&
          this.checkIdentity &&
          this.form.name.value &&
					this.form.insurer.value
    }
  }
}
</script>

<style lang="scss" scoped>
  @import '@/assets/styles/mixin.scss';

  .AddTheInsured {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 19951105;
    .cover {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      background: #333333;
      opacity: .58;
      z-index: 1;
    }
    .content {
      position: absolute;
      top: 50%;
      left: 50%;
      margin: -271px -302px;
      padding: 0 74px 0 30px;
      background: #FFFFFF;
      width: 504px;
      height: 542px;
      border-radius: 4px;
      z-index: 2;
      .ttl {
        margin: 20px 0 25px;
        font-size: 20px;
        color: $color-text;
        font-weight: 600;
      }
      .input-box {
        text-align: right;
        .input-item {
          margin: 22px 0;
        }
        .cut-line {
          margin: -7px 0;
          font-size: 14px;
          color: #D5D8D8;
          line-height: 14px;
          height: 14px;
          margin-left: 46px;
          text-align: left;
        }
        .lb, .input, .input-group {
          vertical-align: top;
        }
        .lb {
          font-size: 14px;
          color: $color-text;
          line-height: 34px;
          padding-right: 6px;
        }
        .input, .input-group {
          display: inline-block;
          text-align: left;
          box-sizing: border-box;
        }
        .input {
          height: 34px;
          width: 364px;
          background: #FFFFFF;
          border-radius: 2px;
        }
        .input-group {
          width: 364px;
          height: auto;
          .input-group-item {
            .ards {
              height: 34px;
            }
            &.address {
              padding-left: 16px;
              margin-bottom: 15px;
              .adrs {
                width: 33.333333333%;
                &:first-of-type {
                  margin-right: 8px;
                  margin-left: -16px;
                }
              }
            }
          }
        }
      }
      .btns {
        margin: 30px 0 20px;
        text-align: right;
        .btn {
          display: inline-block;
          height: 34px;
          width: 80px;
          border-radius: 18px;
          border: 1px solid;
          text-align: center;
          line-height: 34px;
          font-size: 14px;
          margin-left: 16px;
          cursor: pointer;
          &.cancel {
            border-color: #D5D8D8;
            color: #A5A8A8;
          }
          &.save {
            border-color: $color-active-text;
            background-color: $color-active-text;
            color: #FFFFFF;
            &.disabled {
              background-color: #EDEDED;
              border-color: #EDEDED;
              color: #FFFFFF;
            }
          }
        }
      }
    }
  }
</style>
